import React from 'react';

import {
  Modal,
  Box,
  Text,
  Flex,
  SecondaryButton,
  AlertButton,
  TextInline,
} from '@cko/primitives';

interface Props {
  closePrompt: () => void;
  closeRuleBuilder: () => void;
  message: string;
}

const RulesBuilderPrompt: React.FC<Props> = ({
  closePrompt,
  closeRuleBuilder,
  message,
}) => {
  return (
    <Modal
      type="alert"
      title={'Youre leaving the rule builder'}
      closeModal={closePrompt}
    >
      <Box pb={2}>
        <Text fontSize={1} lineHeight={1}>
          {message}
        </Text>
      </Box>
      <Box pb={4}>
        <Text fontSize={1} lineHeight={1}>
          Would you like to{' '}
          <TextInline fontWeight="500">Return and finish</TextInline> your rule,
          or click <TextInline fontWeight="500">Exit rule builder</TextInline>{' '}
          to discard your changes?
        </Text>
      </Box>
      <Flex alignItems="center" p={0}>
        <Box>
          <SecondaryButton onClick={closePrompt} data-testid="delete-button">
            Return and finish
          </SecondaryButton>
        </Box>
        <Box pl={3}>
          <AlertButton
            onClick={() => {
              closePrompt();
              closeRuleBuilder();
            }}
          >
            Exit rule builder
          </AlertButton>
        </Box>
      </Flex>
    </Modal>
  );
};

export default RulesBuilderPrompt;
