import { Box, FormLabel, SegmentedControl } from '@cko/primitives';
import FieldValidationError from 'components/FieldValidationError';
import { useField, useFormikContext } from 'formik';
import React from 'react';

type Props = Omit<React.ComponentProps<typeof SegmentedControl>, 'onChange'> & {
  label?: string;
  name: string;
};

const FormikSegmentedControl: React.FC<Props> = ({ label, name, ...props }) => {
  const [field, meta, { setTouched }] = useField<string>({
    name,
  });
  const { error: errorMessage } = meta;

  const { submitCount } = useFormikContext();
  const showError = submitCount > 0 && errorMessage !== undefined;

  return (
    <>
      {label && <FormLabel>{label}</FormLabel>}
      <SegmentedControl
        errors={showError}
        {...props}
        onChange={(e) => {
          field.onChange(name)(e.target.value);
          setTouched(true, false);
        }}
        selectedValue={field.value}
      />
      {showError && (
        <Box mt={2}>
          <FieldValidationError data-testid={`${name}-error-messsage`}>
            {errorMessage}
          </FieldValidationError>
        </Box>
      )}
    </>
  );
};

export default FormikSegmentedControl;
