import React, {
  useContext,
  createContext,
  useMemo,
  useCallback,
  useState,
} from 'react';

import {
  createEditor,
  Transforms,
  Editor,
  Range,
  Node,
  Descendant,
  Point,
} from 'slate';
import { withReact, ReactEditor } from 'slate-react';
import { withHistory, HistoryEditor } from 'slate-history';

export interface RulesEditorContext {
  editor: Editor & ReactEditor;
  insertPredicate: (predicate: string) => void;
  insertPlaceholder: (length: number) => void;
  removePlaceholder: () => void;
  captureSelection: () => void;
  readOnly: boolean;
  setReadOnly: React.Dispatch<React.SetStateAction<boolean>>;
}

export const RulesBuilderHelpContext = createContext<
  RulesEditorContext | undefined
>(undefined);

const removePlaceholderNodes = (
  editor: Editor & HistoryEditor & ReactEditor
) => {
  const nodes = Editor.nodes(editor, {
    at: [],
    match: (n) => n.type === 'Placeholder',
  });

  for (const node of nodes) {
    const at = node[1];
    HistoryEditor.withoutSaving(editor, () => {
      Transforms.removeNodes(editor, { at });
    });
  }
};

export const RulesEditorContextProvider: React.FunctionComponent = ({
  children,
}) => {
  const [selection, setSelection] = useState<Range | null>(null);
  const [fragment, setFragment] = useState<Descendant[] | null>(null);
  const [readOnly, setReadOnly] = useState(false);

  const editor = useMemo(() => withReact(withHistory(createEditor())), []);

  const captureSelection = useCallback(() => {
    setSelection(editor.selection);
    editor.selection && setFragment(Node.fragment(editor, editor.selection));
  }, [editor]);

  const restoreSelection = useCallback(() => {
    const editorEnd = Editor.end(editor, []);

    if (selection) {
      const isOutsideEditorRange = Point.isAfter(
        Range.end(selection),
        editorEnd
      );
      if (isOutsideEditorRange) {
        Transforms.select(editor, editorEnd);
      } else {
        Transforms.select(editor, selection);
      }
    } else {
      Transforms.select(editor, editorEnd);
    }
  }, [editor, selection]);

  const removePlaceholder = useCallback(() => {
    removePlaceholderNodes(editor);
    HistoryEditor.withoutSaving(editor, () => {
      fragment && Transforms.insertFragment(editor, fragment);
    });
    restoreSelection();
  }, [editor, fragment, restoreSelection]);

  const insertPredicate = useCallback(
    (predicate: string) => {
      removePlaceholderNodes(editor);

      HistoryEditor.withoutSaving(editor, () => {
        fragment && Transforms.insertFragment(editor, fragment);
      });

      ReactEditor.focus(editor);

      restoreSelection();

      Transforms.insertText(editor, predicate);

      Transforms.collapse(editor, { edge: 'end' });

      captureSelection();

      setFragment(null);
    },
    [captureSelection, editor, fragment, restoreSelection]
  );

  const insertPlaceholder = useCallback(
    (length: number) => {
      const placeholderPredicate = ' '.repeat(length);

      ReactEditor.focus(editor);

      HistoryEditor.withoutSaving(editor, () => {
        Transforms.insertNodes(editor, {
          type: 'Placeholder',
          text: placeholderPredicate,
        });
      });
    },
    [editor]
  );

  const value = useMemo(() => {
    return {
      editor,
      insertPredicate,
      insertPlaceholder,
      removePlaceholder,
      captureSelection,
      readOnly,
      setReadOnly,
    };
  }, [
    captureSelection,
    editor,
    insertPlaceholder,
    insertPredicate,
    readOnly,
    removePlaceholder,
  ]);

  return (
    <RulesBuilderHelpContext.Provider value={value}>
      {children}
    </RulesBuilderHelpContext.Provider>
  );
};

export const useRulesEditor = () => {
  const context = useContext(RulesBuilderHelpContext);

  if (context === undefined) {
    throw new Error(
      'useRulesEditor must be used within a RulesEditorContextProvider'
    );
  }

  return context;
};
