import endpoints from 'api/endpoints';
import { AddListEntryFormData } from 'forms/AddListEntryForm/AddListEntryForm';
import { useAddUpdateMutation } from '../common/useAddUpdateMutation';

export const useAddListEntry = (onSuccess: () => void) => {
  return useAddUpdateMutation<AddListEntryFormData, {}>(
    'post',
    endpoints.lists,
    { onSuccess }
  );
};
