import React from 'react';
import { rulesIconMap, rulesTypeTitleMap } from './../rulesBuilderUtils';
import { Flex, Box, Text } from '@cko/primitives';
import RulesExample from './../RulesEditor/RulesExample';
import styled from 'styled-components';

interface Props {
  type: string;
  predicate?: string;
  content: string[];
  title?: string;
}

const StyledHelpContent = styled(Text)(
  ({ theme }) => `
  line-height: 22px;
  color: ${theme.colors.focusLight};

  b {
    font-weight: 500;
    color: black;
  }

  i {
    font-style: italic;
  }
  `
);

export const HelpContent: React.FunctionComponent = ({ children }) => {
  return (
    <StyledHelpContent
      dangerouslySetInnerHTML={{ __html: children as string }}
    />
  );
};

const RulesBuilderHelpContent: React.FunctionComponent<Props> = ({
  type,
  predicate,
  content,
  title,
}) => {
  const Icon = rulesIconMap[type];

  return (
    <>
      <Flex alignItems="center" mb={predicate ? 4 : 3}>
        {Icon && (
          <Box mr={2} style={{ lineHeight: 0 }}>
            <Icon />
          </Box>
        )}
        <Text lineHeight={0} color="focus" fontWeight="500">
          {title || rulesTypeTitleMap[type]}
        </Text>
      </Flex>

      {predicate && (
        <Box mb={3} style={{ wordBreak: 'break-all' }}>
          <RulesExample predicate={predicate} />
        </Box>
      )}

      {content.map((text, i) => {
        return (
          <Box mb={3} key={`content-${i}`}>
            <HelpContent>{text}</HelpContent>
          </Box>
        );
      })}
    </>
  );
};

export default RulesBuilderHelpContent;
