import React from 'react';
import { Modals } from 'types/enums/modals';
import Section from 'components/Section';
import { ParagraphText, Strong } from 'components/Text';
import InfoModal from 'components/InfoModal';
import UnorderedList from 'styles/UnorderedList';
import { ListItem } from '@cko/primitives';

const RulesInfoModalContainer = () => (
  <InfoModal modalKey={Modals.RULE_PROFILE_INFO} title={`Rules`}>
    <article>
      <Section>
        <ParagraphText>
          <Strong>Rules</Strong> are the main building blocks for
          <Strong> Routing</Strong>
          and <Strong> Risk profiles</Strong>.
        </ParagraphText>

        <ParagraphText>
          They take a form of an expression or a collection of expressions that
          can be evaluated and found true or false. When adding a rule to the
          Routing or to a Risk profile you will be able to define a specific
          consequence of that evaluation.
        </ParagraphText>

        <ParagraphText>
          <Strong> Rules can be based on:</Strong>
          <UnorderedList pl={3}>
            <ListItem>
              information within a transaction payload
              <Strong> E.g. Amount &gt; 100</Strong>
            </ListItem>
            <ListItem>
              information provided within a transaction meta-data section (the
              section you can populate with any information you deem necessary)
              <Strong> E.g. Product code equals 14563</Strong>
            </ListItem>
            <ListItem>
              additional contextual information
              <Strong> E.g. Billing address is a valid</Strong>
            </ListItem>
            <ListItem>
              statistical data derived from you traffic
              <Strong>
                E.g. Card used more than 3 times in the last 10 minutes
              </Strong>
            </ListItem>
          </UnorderedList>
        </ParagraphText>
      </Section>
    </article>
  </InfoModal>
);

export default RulesInfoModalContainer;
