import React, { ReactNode } from 'react';
import ReactDOM from 'react-dom';
import styled, { css } from 'styled-components';

import { IconActionCross } from '@cko/icons';
import ModalHeader from './ModalHeader';

import { ClickableElement, TTheme } from '@cko/primitives';

export const Overlay = styled.div`
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: auto;
  z-index: 9999;
  background: rgba(30, 36, 46, 0.7);
  display: flex;
  justify-content: center;
  align-items: flex-start;
  align-content: flex-start;
`;

const modalBorderMap: Record<ModalType, 'success' | 'danger'> = {
  success: 'success',
  alert: 'danger',
};

const modalBorder = (props: ContentProp & { theme: TTheme }) => {
  const borderColor = (props.type && modalBorderMap[props.type]) || 'success';

  return css`
    border-top: ${props.theme.borders[borderColor][2]};
  `;
};

export const Content = styled.div<ContentProp>`
  border-radius: ${(props) => props.theme.radii[1]};
  background: #fff;
  max-width: ${(props) => props.maxWidth};
  min-width: 702px;
  flex: 1;
  padding: 0;
  margin: 24px 0;
  position: relative;
  ${modalBorder}
`;

export const ModalContent = styled.div<ContentProp>`
  border-radius: ${(props) => props.theme.radii[1]};
  max-width: ${(props) => props.maxWidth};
  min-width: 702px;
  flex: 1;
  position: relative;
  z-index: 0;
`;

const CloseButtonHitBox = '4px';

export const CloseButton = styled(ClickableElement)`
  float: right;
  padding: ${CloseButtonHitBox};
  margin-right: -${CloseButtonHitBox};
  margin-top: -${CloseButtonHitBox};
  margin-left: ${CloseButtonHitBox};
  margin-bottom: ${CloseButtonHitBox};
  font-size: 16px;
  line-height: 1;
  svg {
    vertical-align: top;
  }
`;

type ModalType = 'success' | 'alert';

interface ContentProp {
  type?: ModalType;
  maxWidth?: string;
}

interface ModalProp {
  title?: string;
  closeModal: () => void;
  closeOnClickOutside?: boolean;
  closeOnEscape?: boolean;
  headerContent: JSX.Element;
}

const Portal = ({ children }: { children: ReactNode }) =>
  ReactDOM.createPortal(children, document.body);

const ModalWithHeader: React.FC<ModalProp & ContentProp> = ({
  title,
  children,
  headerContent,
  closeModal,
  maxWidth = '400px',
  type = 'success',
  closeOnClickOutside = true,
  closeOnEscape = true,
}) => (
  <Portal>
    <Overlay
      onClick={(event) => {
        event.stopPropagation();
        closeOnClickOutside && closeModal();
      }}
      onKeyUp={(event) => {
        if (event.keyCode === 27) {
          event.stopPropagation();
          closeOnEscape && closeModal();
        }
      }}
      tabIndex={-1}
      role="dialog"
      aria-labelledby="modal-title"
      aria-describedby="modal-content"
      aria-modal="true"
    >
      <Content
        onClick={(event) => event.stopPropagation()}
        type={type}
        maxWidth={maxWidth}
      >
        <ModalHeader
          title={title}
          headerContent={headerContent}
          closeButton={
            <CloseButton
              onClick={(event) => {
                event.stopPropagation();
                closeModal();
              }}
            >
              <IconActionCross />
            </CloseButton>
          }
        />
        <ModalContent id="modal-content">{children}</ModalContent>
      </Content>
    </Overlay>
  </Portal>
);

export default ModalWithHeader;
