import React, { useMemo, ReactNode } from 'react';
import {
  Table,
  TableHead,
  TableBody,
  TableRow,
  TableCell,
} from '@cko/primitives';
import styled from 'styled-components';
import DeleteButton from 'components/DeleteButton';

interface Props<T> {
  items: (T & { id: string })[];
  itemsToDisplay: Partial<keyof (T & { id: string })>[];
  cyTag: string;
  tableHeaders: string[];
  onDeleteEntry?: (item: T) => void;
  canDeleteEntry?: boolean;
  onInteraction?: (item: T) => void;
}

const TableWrapper = styled.div`
  overflow-y: scroll;
  scrollbar-width: thin;

  ::-webkit-scrollbar {
    width: 5px;
    background: transparent; /* Optional: just make scrollbar invisible */
  }

  ::-webkit-scrollbar-thumb {
    background: #ccc;
  }
`;

const EmptyTableCell = styled(TableCell)`
  text-align: center;
  padding: 0 !important;
`;

const DataTable = <T extends {}>({
  items,
  itemsToDisplay,
  cyTag,
  onDeleteEntry,
  canDeleteEntry = false,
  tableHeaders,
  onInteraction,
}: Props<T> & { children?: ReactNode }) => {
  const tableContent = useMemo(() => {
    if (items.length === 0) {
      return (
        <TableRow>
          <EmptyTableCell colSpan={3}>No results</EmptyTableCell>
        </TableRow>
      );
    }

    return items.map((item, i) => {
      return (
        <TableRow
          data-cy={cyTag}
          key={i}
          onInteraction={onInteraction ? () => onInteraction(item) : undefined}
        >
          {Object.keys(item).map(
            (k) =>
              itemsToDisplay.includes(k as keyof typeof item) && (
                <TableCell
                  key={`${k}-${i}`}
                  style={{
                    cursor: onInteraction ? 'pointer' : 'default',
                  }}
                  width={`${100 / itemsToDisplay.length}%`}
                >
                  {item[k as keyof typeof item]}
                </TableCell>
              )
          )}
          {canDeleteEntry && onDeleteEntry && (
            <TableCell width={1 / 100}>
              <DeleteButton onClick={() => onDeleteEntry(item)} />
            </TableCell>
          )}
        </TableRow>
      );
    });
  }, [
    canDeleteEntry,
    cyTag,
    items,
    onDeleteEntry,
    itemsToDisplay,
    onInteraction,
  ]);

  const tableHeaderContent = useMemo(() => {
    const headers = canDeleteEntry ? [...tableHeaders, ''] : tableHeaders;

    return (
      <TableRow>
        {headers.map((header, index) => {
          return (
            <TableCell
              as="th"
              key={header}
              width={
                canDeleteEntry && headers.length - 1 !== index
                  ? `${100 / itemsToDisplay.length}%`
                  : undefined
              }
            >
              {header}
            </TableCell>
          );
        })}
      </TableRow>
    );
  }, [canDeleteEntry, itemsToDisplay.length, tableHeaders]);

  return (
    <TableWrapper>
      <Table>
        <TableHead>{tableHeaderContent}</TableHead>
        <TableBody>{tableContent}</TableBody>
      </Table>
    </TableWrapper>
  );
};

export default DataTable;
