import styled from 'styled-components';
import { ClickableElement } from '@cko/primitives';

const BaseTab = styled(ClickableElement)(({ theme }) => {
  return `
      height: 36px;
      font-size: 14px;
      font-weight: 500;
      border-top-left-radius: ${theme.radii};
      border-top-right-radius: ${theme.radii};
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
      padding: ${theme.space[0]} ${theme.space[3]};
      background-color: ${theme.colors.white};
      border-top: 3px solid transparent;
      color: ${theme.colors.base};

      &:disabled {
        background-color: ${theme.colors.baseLight};
      }

      &:focus {
        border-top: ${theme.borders.success[2]};
      }

      &[aria-selected='true'] {
        border-top: ${theme.borders.success[2]};
        color: ${theme.colors.success};

        &:hover:enabled {
          border-top: ${theme.borders.success[2]};
        }        
      }

      &:hover:enabled {
        border-top: ${theme.borders.base[2]};
      }     
  `;
});

export default BaseTab;
