import useTabs from 'hooks/useTabs';
import React from 'react';

type Props = {
  id: string;
};

const RulesMenuTabPanel: React.FC<Props> = ({ children, id }) => {
  const { getTabPanelProps } = useTabs();

  return <div {...getTabPanelProps({ id })}>{children}</div>;
};

export default RulesMenuTabPanel;
