import React from 'react';
import { Panel } from '@cko/primitives';
import useTabs from 'hooks/useTabs';

type Props = {
  tabId: string;
};

const TabPanel: React.FunctionComponent<Props> = ({ children, tabId }) => {
  const { getTabPanelProps } = useTabs();
  return (
    <Panel p={0} {...getTabPanelProps({ id: tabId })}>
      {children}
    </Panel>
  );
};

export default TabPanel;
