import React from 'react';
import { UnorderedList } from '@cko/primitives';
import styled from 'styled-components';
import IndexChildren from 'components/IndexChildren';

const BaseTabList = styled(UnorderedList)(({ theme }) => {
  return `
  display: flex;

  li {
    margin-right: ${theme.space[2]};
  }

  li:last-child {
    margin-right: 0;
  }`;
});

const TabList: React.FunctionComponent = ({ children }) => {
  return (
    <BaseTabList role="tablist">
      <IndexChildren>{children}</IndexChildren>
    </BaseTabList>
  );
};

export default TabList;
