import React, { useCallback, useMemo, ReactNode } from 'react';
import { useField, useFormikContext } from 'formik';
import isEqual from 'lodash/isEqual';
import {
  SelectMenu,
  SelectMenuButton,
  SelectMenuList,
  SelectMenuItem,
  FormLabel,
  Box,
} from '@cko/primitives';

import FieldValidationError from 'components/FieldValidationError';

type Option<Value> = { label: string; value: Value };

interface Props<Value = unknown> {
  options: Option<Value>[];
  label?: string;
  name: string;
  placeholder: string;
  getItemLabel?: (item: Option<Value>) => string;
  getItemValue?: (item: Option<Value>) => Value | Option<Value>;
}

const FormikSelectMenu = <V extends any = string>({
  options,
  label,
  name,
  placeholder,
  getItemLabel = (item) => (item ? item.label : ''),
  getItemValue = (item) => item?.value ?? item,
}: Props<V> & { children?: ReactNode }) => {
  const [, meta, { setValue, setTouched }] = useField(name);
  const { error: errorMessage } = meta;
  const { submitCount, values } = useFormikContext<any>();
  const showError = submitCount > 0 && errorMessage !== undefined;

  const handleOnChange = useCallback(
    async (item) => {
      await setValue(getItemValue(item));
      setTouched(true);
    },
    [getItemValue, setTouched, setValue]
  );

  const selected = useMemo(
    () => options.find((x) => isEqual(getItemValue(x), values[name])) ?? null,
    [getItemValue, name, options, values]
  );

  return (
    <>
      {label && <FormLabel>{label}</FormLabel>}
      <SelectMenu
        placeholder={placeholder}
        onChange={handleOnChange}
        selectedItem={selected}
        itemToString={getItemLabel}
      >
        <SelectMenuButton hasErrors={showError} />
        <SelectMenuList>
          {options.map((item, i: number) => (
            <SelectMenuItem item={item} index={i} key={`formik-select-${i}`} />
          ))}
        </SelectMenuList>
      </SelectMenu>
      {showError && (
        <Box mt={2}>
          <FieldValidationError data-testid={`${name}-error-messsage`}>
            {errorMessage}
          </FieldValidationError>
        </Box>
      )}
    </>
  );
};

export default FormikSelectMenu;
