import React, { useMemo } from 'react';
import Flex from 'components/Flex';
import { Box, PrimaryButton, TextHeadingThree } from '@cko/primitives';
import { IconActionAdd } from '@cko/icons';
import styled from 'styled-components';
import usePermissions from 'hooks/usePermissions';
import { FormMode } from 'types/models';
import { useRulesBuilderModal } from 'features/RulesBuilder/RulesBuilderContainer/RulesBuilderContainer';

const AddIcon = styled(IconActionAdd)`
  margin-right: 5px;
  font-size: 16px;
  vertical-align: text-bottom;
  & > path {
    fill: white;
  }
`;

const RulesHeader: React.FunctionComponent = () => {
  const { openModal } = useRulesBuilderModal();

  const { hasPermission } = usePermissions();

  const hideCreationButtons = useMemo(() => {
    return !hasPermission('prism:rule:write');
  }, [hasPermission]);

  return (
    <Flex alignItems="center" justifyContent="space-between">
      <Flex alignItems="center" flex="1 0 auto">
        <Box>
          <TextHeadingThree>Rules</TextHeadingThree>
        </Box>
      </Flex>
      {!hideCreationButtons && (
        <PrimaryButton
          onClick={() => {
            openModal({ mode: FormMode.Add, modalId: 'Rule:RulesContainer' });
          }}
          data-cy="create-rule-button"
        >
          <AddIcon />
          Create new rule
        </PrimaryButton>
      )}
    </Flex>
  );
};

export default RulesHeader;
