import React, { useMemo, useRef, useState } from 'react';

type Tab = {
  id: string;
  index: number;
  disabled?: boolean;
};

interface TabWithRef extends Tab {
  ref: React.RefObject<HTMLButtonElement>;
}

interface ITabsContext {
  setActiveTab: React.Dispatch<React.SetStateAction<number>>;
  registerTab: (tab: Tab) => void;
  handleKeyDown: (index: number, keyCode: number) => void;
  isActiveTab: (index: number | undefined) => boolean;
  getTabRef: (id: string) => React.RefObject<HTMLButtonElement> | undefined;
}

export const TabsContext = React.createContext<ITabsContext | undefined>(
  undefined
);

interface Props {
  defaultIndex: number;
  direction?: 'horizontal' | 'vertical';
}

enum KeyCodes {
  LEFT_ARROW = 37,
  RIGHT_ARROW = 39,
  UP_ARROW = 38,
  DOWN_ARROW = 40,
}

export const TabsProvider: React.FunctionComponent<Props> = ({
  children,
  defaultIndex = 0,
  direction = 'horizontal',
}) => {
  const [activeTabIndex, setActiveTab] = useState(defaultIndex);
  const tabs = useRef<TabWithRef[]>([]);

  const prevArrow =
    direction === 'horizontal' ? KeyCodes.LEFT_ARROW : KeyCodes.UP_ARROW;
  const nextArrow =
    direction === 'horizontal' ? KeyCodes.RIGHT_ARROW : KeyCodes.DOWN_ARROW;

  const value = useMemo(() => {
    const registerTab = (tab: Tab) => {
      if (!tabs.current.some((t) => t.id === tab.id)) {
        tabs.current = [...tabs.current, { ...tab, ref: React.createRef() }];
      }
    };

    const handleKeyDown = (index: number, keyCode: number) => {
      if (keyCode === prevArrow || keyCode === nextArrow) {
        const remainingTabs =
          keyCode === nextArrow
            ? tabs.current.slice(index + 1)
            : tabs.current.slice(0, index).reverse();

        const nextTab = remainingTabs.find((tab) => !tab.disabled);

        if (nextTab) {
          setActiveTab(nextTab.index);
          nextTab.ref.current?.focus();
        }
      }
    };

    const isActiveTab = (index: number | undefined) => activeTabIndex === index;

    const getTabRef = (id: string) =>
      tabs.current.find((x) => x.id === id)?.ref;

    return {
      setActiveTab,
      registerTab,
      handleKeyDown,
      isActiveTab,
      getTabRef,
    };
  }, [activeTabIndex, nextArrow, prevArrow, tabs]);

  return <TabsContext.Provider value={value}>{children}</TabsContext.Provider>;
};
