import { ANTLRInputStream, CommonTokenStream } from 'antlr4ts';
import { PredicateLexer } from 'Prism.PredicateParser/PredicateLexer';
import { NormalizedToken } from './SyntaxHighlightEditable';

const tokenize = (text: string) => {
  const chars = new ANTLRInputStream(text);
  const lexer = new PredicateLexer(chars);
  const tokens = new CommonTokenStream(lexer);

  const normaliseTokens = (tokens: CommonTokenStream): NormalizedToken[] => {
    if (!tokens) {
      return [];
    }

    const t = tokens
      .getTokens()
      .map(
        (t) =>
          ({
            id: t.type,
            type: lexer.vocabulary.getSymbolicName(t.type),
            content: t.text,
          } as NormalizedToken)
      )
      .filter((t) => t.id !== -1);

    return t;
  };

  try {
    tokens.fill();
  } catch (e) {}

  return normaliseTokens(tokens);
};

export { tokenize };
