import React, { useCallback } from 'react';
import { Editable } from 'slate-react';
import { EditableProps } from 'slate-react/dist/components/editable';
import { NodeEntry, Node, Text, Range } from 'slate';

export interface NormalizedToken {
  id: number;
  type: string;
  content: string;
}

type Props = EditableProps & {
  tokenize: (text: string) => NormalizedToken[];
  tokensToSkip?: string[];
};

const SyntaxHighlightEditable: React.FunctionComponent<Props> = ({
  tokenize,
  tokensToSkip,
  ...rest
}) => {
  const decorate = useCallback(
    ([node, path]: NodeEntry<Node>) => {
      const ranges: Range[] = [];

      if (!Text.isText(node)) {
        return ranges;
      }

      if (!node.text.length) {
        return ranges;
      }

      const tokens = tokenize(node.text);

      let start = 0;

      for (const token of tokens) {
        const length = token.content.length;
        const end = start + length;

        if (
          !tokensToSkip ||
          (tokensToSkip && !tokensToSkip.includes(token.type))
        ) {
          ranges.push({
            type: token.type,
            value: token.content,
            anchor: { path, offset: start },
            focus: { path, offset: end },
          });
        }

        start = end;
      }

      return ranges;
    },
    [tokenize, tokensToSkip]
  );

  return <Editable decorate={decorate} {...rest} />;
};

export default SyntaxHighlightEditable;
