import useIntentHover from 'hooks/useIntentHover';
import { useRulesBuilderHelp } from '../RulesBuilderHelpContext';
import { useRulesEditor } from '../RulesEditorContext';
import { useCallback } from 'react';
import { RuleProperties } from 'types/models';
import { useVelocityWizard } from '../VelocityWizardContext';

const usePropertyList = () => {
  const getHoverProps = useIntentHover();
  const { setHelpContentKey } = useRulesBuilderHelp();
  const {
    captureSelection,
    removePlaceholder,
    insertPlaceholder,
    insertPredicate,
    setReadOnly,
  } = useRulesEditor();
  const { isDrawerOpen } = useVelocityWizard();

  return useCallback(
    (ruleProperties: RuleProperties) => {
      const { onMouseEnter, onMouseLeave } = getHoverProps({
        onEnter: () => {
          setHelpContentKey(ruleProperties.helpKey);
          ruleProperties.predicate &&
            insertPlaceholder(ruleProperties.predicate.length);
        },
        onLeave: () => {
          removePlaceholder();
        },
      });

      return {
        onMouseEnter: () => {
          captureSelection();
          setReadOnly(true);
          !isDrawerOpen && onMouseEnter();
        },
        onMouseLeave: () => {
          setReadOnly(false);
          !isDrawerOpen && onMouseLeave();
        },
        onClick: () => {
          !isDrawerOpen &&
            ruleProperties.predicate &&
            insertPredicate(ruleProperties.predicate);
        },
      };
    },
    [
      captureSelection,
      getHoverProps,
      insertPlaceholder,
      insertPredicate,
      isDrawerOpen,
      removePlaceholder,
      setHelpContentKey,
      setReadOnly,
    ]
  );
};

export default usePropertyList;
