import React from 'react';
import { Flex, Box, Text } from '@cko/primitives';
import RulesExample from '../RulesEditor/RulesExample';
import { HelpContent } from './RulesBuilderHelpContent';
import VelocitiesIcon from '../RulesNavigationMenu/VelocitiesIcon';

interface Props {
  content: string[];
  title: string;
  predicate: string;
  exampleDesc: string;
}

const VelocityWizardHelpContent: React.FunctionComponent<Props> = ({
  content,
  title,
  predicate,
  exampleDesc,
}) => {
  return (
    <>
      <Flex alignItems="center" mb={predicate ? 4 : 3}>
        <Box mr={2} style={{ lineHeight: 0 }}>
          <VelocitiesIcon />
        </Box>

        <Text lineHeight={0} color="focus" fontWeight="500">
          {title}
        </Text>
      </Flex>

      {content.map((text, i) => {
        return (
          <Box mb={3} key={`content-${i}`}>
            <HelpContent>{text}</HelpContent>
          </Box>
        );
      })}

      <Box mb={3} style={{ wordBreak: 'break-all' }}>
        <RulesExample predicate={predicate} />
      </Box>

      <Box mb={3}>
        <Text>{exampleDesc}</Text>
      </Box>
    </>
  );
};

export default VelocityWizardHelpContent;
