import { useQuery } from 'react-query';
import { ListSummary } from 'types/models';
import endpoints from 'api/endpoints';
import useGetRequest from 'hooks/api/common/useGetRequest';
import { AxiosError } from 'axios';

export interface ListSummariesResponse {
  summaries: ListSummary[];
}

export const useListsData = () => {
  const req = useGetRequest<ListSummariesResponse>();
  return useQuery<ListSummariesResponse, AxiosError<ListSummariesResponse>>(
    endpoints.lists,
    () => req(endpoints.lists)
  );
};
