import React, { useContext, createContext, useMemo, useState } from 'react';
import RulebuilderContent from '../RuleBuilderContent';

export interface RulesBuilderContext {
  helpContentKey: string;
  setHelpContentKey: React.Dispatch<React.SetStateAction<string>>;
}

export const RulesBuilderHelpContext = createContext<
  RulesBuilderContext | undefined
>(undefined);

export const RulesBuilderHelpProvider: React.FunctionComponent = ({
  children,
}) => {
  const [helpContentKey, setHelpContentKey] = useState(
    RulebuilderContent.exampleCombinations.default.helpKey
  );

  const value = useMemo(() => {
    return {
      helpContentKey,
      setHelpContentKey,
    };
  }, [helpContentKey]);

  return (
    <RulesBuilderHelpContext.Provider value={value}>
      {children}
    </RulesBuilderHelpContext.Provider>
  );
};

export const useRulesBuilderHelp = () => {
  const context = useContext(RulesBuilderHelpContext);

  if (context === undefined) {
    throw new Error(
      'useRulesBuilderHelp must be used within a RulesBuilderHelpProvider'
    );
  }

  return context;
};
