import React, { useContext, createContext, useMemo, useState } from 'react';
import { useAvailablePropertiesData } from 'hooks/api/rules';
import { useListsData } from 'hooks/api/lists';
import {
  AvailableProperty,
  ListSummary,
  ScoringRuleFormData,
} from 'types/models';
import { Flex } from '@cko/primitives';
import Loader from 'components/Loader';
import useMlScoreEnabled from 'hooks/useMlScoreEnabled';
import { RulesBuilderHelpProvider } from '../RulesBuilderHelpContext';
import { RulesEditorContextProvider } from '../RulesEditorContext';
import { AddRuleResponse } from 'hooks/api/rules/useAddRule';
import { Formik } from 'formik';
import useRuleValidator from '../RulesBuilderForm/useRuleValidator';
import { ScrollSpyProvider } from 'hooks/useScrollSpy';

interface IRulesBuilderContext {
  availableProperties: AvailableProperty[];
  availableLists: ListSummary[];
  submissionError: string | null;
  setSubmissionError: React.Dispatch<React.SetStateAction<string | null>>;
}

export const RulesBuilderContext = createContext<
  IRulesBuilderContext | undefined
>(undefined);

interface Props {
  initalFormValues: {
    name: string;
    predicate: string;
  };
  handleSubmit: (
    values: ScoringRuleFormData
  ) => Promise<AddRuleResponse | undefined>;
}

export const RulesBuilderProvider: React.FunctionComponent<Props> = ({
  children,
  initalFormValues,
  handleSubmit,
}) => {
  const {
    data: rulesData,
    status: availablePropertiesStatus,
  } = useAvailablePropertiesData();

  const enabledSelectedMerchantMlScore = useMlScoreEnabled();

  const { data: listsData, status: listsStatus } = useListsData();

  const loading = useMemo(
    () => availablePropertiesStatus === 'loading' || listsStatus === 'loading',
    [availablePropertiesStatus, listsStatus]
  );

  const [submissionError, setSubmissionError] = useState<string | null>(null);

  const ruleValidator = useRuleValidator();

  const value = useMemo(() => {
    const availableProperties = rulesData?.properties ?? [];
    const availableLists = listsData?.summaries ?? [];
    return {
      availableProperties: enabledSelectedMerchantMlScore
        ? availableProperties
        : availableProperties.filter((availableProperty) => {
            return availableProperty.name !== 'score';
          }),
      availableLists,
      submissionError,
      setSubmissionError,
    };
  }, [rulesData, listsData, submissionError, enabledSelectedMerchantMlScore]);

  if (loading) {
    return (
      <Flex justifyContent="center" height="70vh">
        <Loader size="large" />
      </Flex>
    );
  }

  return (
    <RulesBuilderContext.Provider value={value}>
      <RulesBuilderHelpProvider>
        <RulesEditorContextProvider>
          <ScrollSpyProvider
            keys={['examples', 'properties', 'operators', 'velocities']}
          >
            <Formik
              initialValues={initalFormValues}
              onSubmit={async (values, actions) => {
                try {
                  setSubmissionError(null);
                  await handleSubmit(values);
                } catch (e) {
                  if (e.response?.status === 409) {
                    setSubmissionError(
                      ` A rule named "${values.name}" already exists.`
                    );
                  } else {
                    setSubmissionError(
                      'Technical error. Please try again later.'
                    );
                  }
                } finally {
                  actions.setSubmitting(false);
                }
              }}
              validate={ruleValidator}
              validateOnChange={false}
            >
              {children}
            </Formik>
          </ScrollSpyProvider>
        </RulesEditorContextProvider>
      </RulesBuilderHelpProvider>
    </RulesBuilderContext.Provider>
  );
};

export const useRulesBuilder = () => {
  const context = useContext(RulesBuilderContext);

  if (context === undefined) {
    throw new Error(
      'useRuleBuilder must be used within a RulesBuilderProvider'
    );
  }

  return context;
};
