import RulesMenuItems from './RulesMenuItems';
import React, { useState } from 'react';
import useModalManager from 'hooks/useModalManager';
import { Modals } from 'types/enums/modals';
import useAlerts from 'hooks/useAlerts';
import RulesBuilderForm from 'features/RulesBuilder/RulesBuilderForm';
import { ScoringRuleFormData, ScoringRuleModalData } from 'types/models';
import { useAddRule } from 'hooks/api/rules';
import { useUpdateRule } from 'hooks/api/rules';
import { queryCache } from 'react-query';
import endpoints from 'api/endpoints';
import { Flex, Box, TTheme } from '@cko/primitives';
import RulesInfoModalContainer from '../RulesInfoModalContainer';
import RulesBuilderHelpContainer from '../RulesBuilderHelpContainer';
import RulesNavigationMenu from '../RulesNavigationMenu';
import { RulesBuilderProvider } from './../RulesBuilderContext';
import { ThemeProvider } from 'styled-components';
import RulesBuilderModal from '../RulesBuilderModal';
import {
  RulesBuilderNamePrompt,
  RulesBuilderRulePrompt,
  RulesBuilderSavePrompt,
} from '../RulesBuilderPrompts';
import { VelocityWizardProvider } from '../VelocityWizardContext';
import { TabsProvider } from 'contexts/TabsContext';

const ruleBuilderBreakpoints = ['640px', '832PX', '1260px'];

interface Props {
  modalId: string;
}

export const useRulesBuilderModal = () => {
  return useModalManager<ScoringRuleModalData>(Modals.ADD_SCORING_RULE);
};

const RulesBuilderContainer: React.FunctionComponent<Props> = ({ modalId }) => {
  const { modalData, closeModal } = useRulesBuilderModal();
  const [showHelpSection, setShowHelpSection] = useState(true);

  const { showAlert } = useAlerts();

  const handleSuccess = () => {
    showAlert(modalData.mode === 'Add' ? 'Rule added' : 'Rule updated');
    closeModal();
    queryCache.invalidateQueries(endpoints.rules);
  };

  const [addRule] = useAddRule(handleSuccess, (values) => {
    modalData.onRequestSuccess &&
      modalData.onRequestSuccess(values, modalData.meta);
  });

  const [updateRule] = useUpdateRule(handleSuccess);

  if (!modalData || modalData.modalId !== modalId) {
    return null;
  }

  const handleSubmit = (values: ScoringRuleFormData) => {
    return modalData.mode === 'Add'
      ? addRule({ data: values })
      : updateRule({ data: values, path: modalData.id });
  };

  const initialFormValues = {
    name: modalData.name || '',
    predicate: modalData.predicate || '',
  };

  return (
    <ThemeProvider
      theme={(theme: TTheme) => ({
        ...theme,
        breakpoints: ruleBuilderBreakpoints,
      })}
    >
      <RulesBuilderProvider
        initalFormValues={initialFormValues}
        handleSubmit={handleSubmit}
      >
        <VelocityWizardProvider>
          <>
            <RulesBuilderModal>
              <Flex height="85vh">
                <Flex flexBasis="360px" maxWidth="360px" mr="24px">
                  <TabsProvider defaultIndex={0} direction="vertical">
                    <RulesNavigationMenu />
                    <RulesMenuItems />
                  </TabsProvider>
                </Flex>
                <Flex
                  pt={3}
                  pb={4}
                  pr={['24px', '24px', '24px', 0]}
                  flex={1}
                  flexDirection={['column', 'column', 'column', 'row']}
                  style={{ overflowY: 'scroll' }}
                >
                  <Flex flexGrow={1} flexDirection="column">
                    <Box pt={3} pb={4} pl={1}>
                      <RulesBuilderForm data={modalData} />
                    </Box>
                  </Flex>
                  <Flex
                    ml={[0, 0, 0, '24px']}
                    flexBasis={showHelpSection ? '360px' : '50px'}
                    flexDirection="column"
                    flexShrink={0}
                  >
                    <RulesBuilderHelpContainer
                      onToggle={() => setShowHelpSection(!showHelpSection)}
                      showHelpSection={showHelpSection}
                    />
                  </Flex>
                </Flex>
              </Flex>
            </RulesBuilderModal>
            <RulesInfoModalContainer />
            <RulesBuilderNamePrompt />
            <RulesBuilderRulePrompt />
            <RulesBuilderSavePrompt
              handleSubmit={handleSubmit}
              formMode={modalData.mode}
            />
          </>
        </VelocityWizardProvider>
      </RulesBuilderProvider>
    </ThemeProvider>
  );
};

export default RulesBuilderContainer;
