import React from 'react';
import { Box, Text, ClickableElement } from '@cko/primitives';
import ExampleRulesIcon from './ExampleRulesIcon';
import PropertiesRulesIcon from './PropertiesRulesIcon';
import styled from 'styled-components';
import 'styled-components/macro';
import OperatorsIcon from './OperatorsIcon';
import VelocitiesIcon from './VelocitiesIcon';
import useTabs from 'hooks/useTabs';
import IndexChildren from 'components/IndexChildren';

const MenuItemWrapper = styled(ClickableElement)`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 90px;
  width: 90px;
  &[aria-selected='true'] {
    background: #e4e9eb;
  }
`;

interface Props {
  children: React.ReactNode;
  icon: JSX.Element;
  index?: number;
  id: string;
}

const MenuItem: React.FunctionComponent<Props> = ({
  children,
  icon,
  index,
  id,
}) => {
  const { getTabProps } = useTabs();

  if (index === undefined) {
    return null;
  }

  return (
    <li>
      <MenuItemWrapper {...getTabProps({ id, index: index! })}>
        <Box mx="auto" mb={2}>
          {icon}
        </Box>
        <Text
          mx={2}
          color="focusLight"
          fontSize={1}
          style={{ textAlign: 'center' }}
        >
          {children}
        </Text>
      </MenuItemWrapper>
    </li>
  );
};

const RulesNavigationMenu = () => {
  return (
    <Box bg="#F9F8FA" borderRight="baseLight.1">
      <ul role="tablist">
        <IndexChildren>
          <MenuItem id="example-rules" icon={<ExampleRulesIcon />}>
            Example rules
          </MenuItem>

          <MenuItem id="properties" icon={<PropertiesRulesIcon />}>
            Properties
          </MenuItem>

          <MenuItem id="operators" icon={<OperatorsIcon />}>
            Operators
          </MenuItem>

          <MenuItem id="velocities" icon={<VelocitiesIcon />}>
            Velocities
          </MenuItem>
        </IndexChildren>
      </ul>
    </Box>
  );
};

export default RulesNavigationMenu;
