import { TabsContext } from 'contexts/TabsContext';
import { useCallback, useContext } from 'react';

const useTabsContext = () => {
  const context = useContext(TabsContext);

  if (context === undefined) {
    throw new Error('useTabs must be used within a TabsContext');
  }

  return context;
};

const useTabs = () => {
  const {
    setActiveTab,
    registerTab,
    handleKeyDown,
    isActiveTab,
    getTabRef,
  } = useTabsContext();

  const onKeyDown = useCallback(
    (index: number) => (event: React.KeyboardEvent<HTMLButtonElement>) => {
      const { keyCode } = event;
      index !== undefined && handleKeyDown(index, keyCode);
    },
    [handleKeyDown]
  );

  const onClick = useCallback(
    (index: number) => (
      event: React.MouseEvent<HTMLButtonElement, MouseEvent>
    ) => {
      event.preventDefault();
      index !== undefined && setActiveTab(index);
    },
    [setActiveTab]
  );

  const getTabListProps = () => {
    return {
      id: 'tablist',
      role: 'tablist',
    };
  };

  const getTabPanelProps = ({ id }: { id: string }) => {
    return {
      id: `tabpanel-${id}`,
      role: 'tabpanel',
      'aria-labelledby': `tab-${id}`,
    };
  };

  const getTabProps = useCallback(
    ({
      index,
      disabled,
      id,
    }: {
      index: number;
      disabled?: boolean;
      id: string;
    }) => {
      registerTab({ index, disabled, id });

      return {
        onKeyDown: onKeyDown(index),
        onClick: onClick(index),
        ref: getTabRef(id),
        tabIndex: isActiveTab(index) ? 0 : -1,
        disabled: disabled && !isActiveTab(index),
        id: `tab-${id}`,
        role: 'tab',
        'aria-selected': isActiveTab(index),
        'aria-controls': `tabpanel-${id}`,
      };
    },
    [getTabRef, isActiveTab, onClick, onKeyDown, registerTab]
  );

  return {
    getTabProps,
    getTabPanelProps,
    getTabListProps,
    isActiveTab,
    setActiveTab,
  };
};

export default useTabs;
