import React from 'react';
import { OutlineButton, Box } from '@cko/primitives';
import { IconActionInfo } from '@cko/icons';
import useModalManager from 'hooks/useModalManager';
import { Modals } from 'types/enums/modals';
import ModalWithHeader from './ModalWithHeader';
import { ScoringRuleFormData } from 'types/models';
import { useFormikContext } from 'formik';
import { useRulesBuilderModal } from '../RulesBuilderContainer/RulesBuilderContainer';

const RulesBuilderModal: React.FC = ({ children }) => {
  const { openModal: openRulesInfo } = useModalManager<string>(
    Modals.RULE_PROFILE_INFO
  );

  const { closeModal: closeRuleBuilderModal } = useRulesBuilderModal();

  const { openModal: openRuleBuilderNamePrompt } = useModalManager<boolean>(
    Modals.RULE_BUILDER_NAME_PROMPT
  );

  const { openModal: openRuleBuilderRulePrompt } = useModalManager<boolean>(
    Modals.RULE_BUILDER_RULE_PROMPT
  );

  const { openModal: openRuleBuilderSavePrompt } = useModalManager<boolean>(
    Modals.RULE_BUILDER_SAVE_PROMPT
  );

  const { errors, touched } = useFormikContext<ScoringRuleFormData>();

  const closeModal = () => {
    const formHasNotBeenTouched = Object.keys(touched).length === 0;
    if (formHasNotBeenTouched) {
      closeRuleBuilderModal();
      return;
    }

    if (errors.name) {
      openRuleBuilderNamePrompt();
    } else if (errors.predicate) {
      openRuleBuilderRulePrompt();
    } else {
      openRuleBuilderSavePrompt();
    }
  };

  return (
    <ModalWithHeader
      type="success"
      title="Create a new rule"
      headerContent={
        // 77px is to align the learn more button with the content of the modal
        <Box ml="77px">
          <OutlineButton onClick={() => openRulesInfo()}>
            <IconActionInfo /> Learn more
          </OutlineButton>
        </Box>
      }
      closeModal={closeModal}
      maxWidth="90vw"
      closeOnClickOutside={false}
      closeOnEscape={false}
    >
      {children}
    </ModalWithHeader>
  );
};

export default RulesBuilderModal;
