import { queryCache } from 'react-query';
import endpoints from 'api/endpoints';
import { PaginationVariables } from 'types/models';

export * from './useAddListEntry';
export * from './useDeleteList';
export * from './useDeleteListEntry';
export * from './useListItemsData';
export * from './useListsData';

export const refetchListsQueries = (
  paginationVariables: PaginationVariables
) => {
  queryCache.invalidateQueries(endpoints.lists, { exact: true });
  queryCache.invalidateQueries([endpoints.lists, paginationVariables], {
    exact: true,
  });
};
