import {
  createValidator,
  required,
  maxLength,
  regexMatch,
} from 'utils/validator';
import endpoints from 'api/endpoints';
import useGetRequest from 'hooks/api/common/useGetRequest';
import useMlScoreEnabled from 'hooks/useMlScoreEnabled';
import { ValidateRuleResponse } from 'types/models';

const errorCodeMap: Record<number, string> = {
  1000: 'Unexpected end of expression',
  5000: 'Unknown token',
  6000: 'Unexpected type',
  6001: 'Type mismatch',
  6002: 'Expected type of number',
  6003: 'Expected type of string',
  6004: 'Expected type of list',
  6005: 'Expected type of boolean',
  7000: 'Non-existent property',
  9000: 'Invalid predicate',
};

const getErrorMessage = (
  errorCode: typeof errorCodeMap,
  sourceText: string
) => {
  return `Error: ${errorCode},  The following text is invalid: ${sourceText}, please make sure that your rule is valid. `;
};

export const useRuleEvaluator = () => {
  const get = useGetRequest<ValidateRuleResponse>();

  const evaluateRule = async (rule: string) => {
    try {
      const result = await get(`${endpoints.rules}/is-valid`, {
        params: { predicate: rule },
      });

      const { is_valid, errors } = result;

      if (is_valid) {
        return null;
      }

      if (errors?.length) {
        const errorMessage = getErrorMessage(
          errorCodeMap[errors[0].error_code],
          errors[0].source_context.offending_text
        );

        return errorMessage;
      }

      return 'Rule could not be validated';
    } catch {
      return 'Rule could not be validated';
    }
  };

  return evaluateRule;
};

const nameValidators = [required('Name is required'), maxLength(100)];

const useRuleValidator = () => {
  const evaluateRule = useRuleEvaluator();

  const isExpressionValid = () => {
    return async (value: string) => {
      const error = await evaluateRule(value);

      return error;
    };
  };

  const mlScoreEnabled = useMlScoreEnabled();

  const mlScoreEnabledValidation = createValidator({
    name: nameValidators,
    predicate: [required('Rule is required'), isExpressionValid()],
  });

  const mlScoreDisabledValidation = createValidator({
    name: nameValidators,
    predicate: [
      required('Rule is required'),
      regexMatch(
        /:score:/,
        'CKO model score is not available to you at the moment. Please contact your customer support manager for details'
      ),
      isExpressionValid(),
    ],
  });

  return mlScoreEnabled ? mlScoreEnabledValidation : mlScoreDisabledValidation;
};

export default useRuleValidator;
