import React, { useState } from 'react';
import useModalManager from 'hooks/useModalManager';
import { Modals } from 'types/enums/modals';
import { ScoringRuleFormData, FormMode } from 'types/models';
import {
  Modal,
  Box,
  Text,
  Flex,
  SecondaryButton,
  AlertButton,
  TextInline,
} from '@cko/primitives';
import { useFormikContext } from 'formik';
import InlineAlert from 'components/InlineAlert';
import { useRulesBuilderModal } from '../RulesBuilderContainer/RulesBuilderContainer';

interface Props {
  handleSubmit: (values: ScoringRuleFormData) => void;
  formMode: FormMode;
}

const RulesBuilderPrompt: React.FC<Props> = ({ handleSubmit, formMode }) => {
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submissionError, setSubmissionError] = useState<string | null>(null);

  const { modalData, closeModal: closePrompt } = useModalManager<boolean>(
    Modals.RULE_BUILDER_SAVE_PROMPT
  );

  const { closeModal: closeRuleBuilder } = useRulesBuilderModal();

  const { values } = useFormikContext<ScoringRuleFormData>();

  if (!modalData) {
    return null;
  }

  const handleSave = async () => {
    try {
      setSubmissionError(null);
      await handleSubmit(values);
      closePrompt();
      closeRuleBuilder();
    } catch (e) {
      if (e.response?.status === 409) {
        setSubmissionError(` A rule named "${values.name}" already exists.`);
      } else {
        setSubmissionError('Technical error. Please try again later.');
      }
      setIsSubmitting(false);
    }
  };

  const ctaText = formMode === 'Add' ? 'Create' : 'Update';

  return (
    <Modal
      type="alert"
      title={'Youre leaving the rule builder'}
      closeModal={closePrompt}
    >
      <Box pb={2}>
        <Text fontSize={1} lineHeight={1}>
          Your rule is complete, however you havent saved and{' '}
          {`${ctaText.toLocaleLowerCase()}d`} it. If you exit now your progress
          will be lost.
        </Text>
      </Box>
      <Box pb={4}>
        <Text fontSize={1} lineHeight={1}>
          Would you like to{' '}
          <TextInline fontWeight="500">{ctaText} your rule</TextInline> , or
          click <TextInline fontWeight="500">Exit rule builder</TextInline> to
          discard your changes?
        </Text>
      </Box>
      <Flex alignItems="center" p={0}>
        <Box>
          <SecondaryButton onClick={handleSave} data-testid="delete-button">
            {isSubmitting ? 'Submitting' : `${ctaText} your rule`}
          </SecondaryButton>
        </Box>
        <Box pl={3}>
          <AlertButton
            onClick={() => {
              closePrompt();
              closeRuleBuilder();
            }}
          >
            Exit rule builder
          </AlertButton>
        </Box>
      </Flex>

      {submissionError && <InlineAlert>{submissionError}</InlineAlert>}
    </Modal>
  );
};

export default RulesBuilderPrompt;
