import React from 'react';
import useModalManager from 'hooks/useModalManager';
import { Modals } from 'types/enums/modals';

import RulesBuilderPrompt from './RulesBuilderPrompt';
import { useRulesBuilderModal } from '../RulesBuilderContainer/RulesBuilderContainer';

const RulesBuilderNamePrompt = () => {
  const { modalData, closeModal } = useModalManager<boolean>(
    Modals.RULE_BUILDER_NAME_PROMPT
  );

  const { closeModal: closeRuleBuilderModal } = useRulesBuilderModal();

  if (!modalData) {
    return null;
  }

  return (
    <RulesBuilderPrompt
      closePrompt={closeModal}
      closeRuleBuilder={closeRuleBuilderModal}
      message="Your rule is incomplete, you havent named it, if you exit now your progress will be lost."
    />
  );
};

export default RulesBuilderNamePrompt;
