import useTabs from 'hooks/useTabs';
import React from 'react';
import useVelocityList from '../RulesBuilderContainer/useVelocityList';
import VelocityPropertyItem from '../RulesBuilderContainer/VelocityPropertyItem';
import { useRulesBuilderHelp } from '../RulesBuilderHelpContext';
import {
  useVelocityWizard,
  VelocityWizardFormType,
} from '../VelocityWizardContext';

const VelocityMenuContainer: React.FunctionComponent = () => {
  const { onFormTypeSelected, formType } = useVelocityWizard();
  const velocityListProps = useVelocityList();
  const { setHelpContentKey } = useRulesBuilderHelp();
  const { getTabPanelProps } = useTabs();

  return (
    <div {...getTabPanelProps({ id: 'velocities' })}>
      <ul>
        <VelocityPropertyItem
          isActive={
            formType === VelocityWizardFormType.VelocityTransactionCount
          }
          title="Transaction count"
          description="- check occurrence of a
  single attribute over a time period "
          content="velocity_count, 1 attribute"
          onClick={() => {
            onFormTypeSelected(VelocityWizardFormType.VelocityTransactionCount);
            setHelpContentKey('velocityWizard.transactionCount');
          }}
          {...velocityListProps('velocityWizard.transactionCount')}
        />
        <VelocityPropertyItem
          isActive={formType === VelocityWizardFormType.VelocityRelative}
          title="Relative"
          description="- check the
occurrence of one attribute in relation to another"
          content="velocity, 2 attributes"
          onClick={() => {
            onFormTypeSelected(VelocityWizardFormType.VelocityRelative);
            setHelpContentKey('velocityWizard.relative');
          }}
          {...velocityListProps('velocityWizard.relative')}
        />
      </ul>
    </div>
  );
};
export default VelocityMenuContainer;
