import React, { useMemo } from 'react';
import { Flex, OutlineButton, Box, TextHeadingOne } from '@cko/primitives';
import FeatureFlag from 'components/FeatureFlag';
import RulesBuilderHelpContent from './../RulesBuilderHelpContent';
import VelocityWizardHelpContent from './../RulesBuilderHelpContent/VelocityWizardHelpContent';
import { findHelpContent } from '../rulesBuilderUtils';
import { useRulesBuilderHelp } from '../RulesBuilderHelpContext';
import styled from 'styled-components';
import { display, DisplayProps } from 'styled-system';

interface Props {
  showHelpSection: boolean;
  onToggle: () => void;
}

const ToggleButton = styled(OutlineButton)<DisplayProps>`
  ${display}
`;

const RulesBuilderHelpContainer: React.FunctionComponent<Props> = ({
  showHelpSection,
  onToggle,
}) => {
  const { helpContentKey } = useRulesBuilderHelp();

  const helpContent = useMemo(() => {
    return findHelpContent(helpContentKey);
  }, [helpContentKey]);

  return (
    <>
      <Flex justifyContent="space-between" pr={3}>
        <FeatureFlag on={showHelpSection}>
          <TextHeadingOne>Help</TextHeadingOne>
        </FeatureFlag>
        <ToggleButton
          onClick={onToggle}
          display={['none', 'none', 'none', 'unset']}
        >
          {showHelpSection ? <>{'>'}</> : <>{'<'}</>}
        </ToggleButton>
      </Flex>

      {helpContent && (
        <FeatureFlag on={showHelpSection}>
          <Box mt={3} pr={3}>
            {helpContent.type === 'velocity' ? (
              <VelocityWizardHelpContent
                title={helpContent.title}
                content={helpContent.content}
                predicate={helpContent.example.predicate}
                exampleDesc={helpContent.example.description}
              />
            ) : (
              <RulesBuilderHelpContent
                type={helpContent.type}
                predicate={helpContent.predicate}
                content={helpContent.content}
                title={helpContent.title}
              />
            )}
          </Box>
        </FeatureFlag>
      )}
    </>
  );
};

export default RulesBuilderHelpContainer;
