import React, { useCallback, useEffect } from 'react';
import {
  useVelocityWizard,
  VelocityWizardFormType,
} from '../VelocityWizardContext';
import SimpleVelocityForm from '../VelocityWizard/SimpleVelocityForm/SimpleVelocityForm';
import VelocityDrawer from '../VelocityDrawer/VelocityDrawer';
import RelativeVelocityForm from '../VelocityWizard/RelativeVelocityForm/RelativeVelocityForm';
import { useRulesEditor } from '../RulesEditorContext';
import { useRulesBuilder } from '../RulesBuilderContext';
import { AvailableProperty } from 'types/models';
import { useScrollSpy } from 'hooks/useScrollSpy';

interface Props {
  formType: VelocityWizardFormType | null;
  handleClose: () => void;
  availableProperties: AvailableProperty[];
  onSubmit: (predicate: string) => void;
}

const VelocityForm: React.FunctionComponent<Props> = ({
  formType,
  handleClose,
  availableProperties,
  onSubmit,
}) => {
  switch (formType) {
    case VelocityWizardFormType.VelocityTransactionCount:
      return (
        <SimpleVelocityForm
          handleClose={handleClose}
          availableProperties={availableProperties}
          onSubmit={onSubmit}
        />
      );
    case VelocityWizardFormType.VelocityRelative:
      return (
        <RelativeVelocityForm
          handleClose={handleClose}
          availableProperties={availableProperties}
          onSubmit={onSubmit}
        />
      );
    default:
      return null;
  }
};

const VelocityWizardContainer: React.FunctionComponent = () => {
  const {
    formType,
    handleClose,
    isDrawerOpen,
    drawerRef,
  } = useVelocityWizard();
  const { insertPredicate } = useRulesEditor();
  const { availableProperties } = useRulesBuilder();
  const { isInView } = useScrollSpy({
    getRoot: (el: Element) => el.parentElement?.parentElement,
  });

  useEffect(() => {
    if (!isInView('velocities')) {
      handleClose();
    }
  }, [handleClose, isInView]);

  const handleSubmit = useCallback(
    (property: string): void => {
      insertPredicate(property);
      handleClose();
    },
    [handleClose, insertPredicate]
  );

  return (
    <VelocityDrawer velocityFormVisible={isDrawerOpen} ref={drawerRef}>
      <VelocityForm
        availableProperties={availableProperties}
        onSubmit={handleSubmit}
        formType={formType}
        handleClose={handleClose}
      />
    </VelocityDrawer>
  );
};

export default VelocityWizardContainer;
