import { usePaginatedQuery } from 'react-query';
import endpoints from 'api/endpoints';
import { ListItem, PaginationVariables } from 'types/models';
import useGetRequest from 'hooks/api/common/useGetRequest';

interface ListItemsResponse {
  alias: string;
  name: string;
  description: string;
  created_at: string;
  total_count: number;
  items: ListItem[];
}

export const useListItemsData = (variables: PaginationVariables) => {
  const req = useGetRequest();

  return usePaginatedQuery<ListItemsResponse, [string, PaginationVariables]>(
    [endpoints.lists, variables],
    (_, { resource, limit, skip, searchQuery }) => {
      return req(
        `${endpoints.lists}/${resource}?limit=${limit}&skip=${skip}&search=${searchQuery}`
      );
    }
  );
};
