import React from 'react';
import {
  Box,
  SpaceBetween,
  Text,
  FormLabel,
  Flex,
  PrimaryButton,
} from '@cko/primitives';
import {
  simpleVelocityProperties,
  transactionActionOptions,
  velocityPeriodOptions,
} from '../velocityProperties';
import { Form, Formik } from 'formik';
import FormikSegmentedControl from 'components/FormikSegmentedControl';
import FormikSelectMenu from 'components/FormikSelectMenu';
import { createValidator, required } from 'utils/validator';
import VelocityFormHeader from '../VelocityFormHeader';
import TooltipButtonWrapper from 'components/TooltipButtonWrapper';
import { AvailableProperty } from 'types/models';

const buildPropertyString = (
  attribute: string,
  period: string,
  action: string
) => `nb_charge_${action}_per_${attribute}_${period}`;

const simpleVelocityValidator = (
  availableProperties: AvailableProperty[]
) => async (values: FormValues) => {
  const fieldErrors = await createValidator({
    attribute: [required()],
    period: [required()],
    action: [required()],
  })(values);

  if (Object.values(fieldErrors).find((e) => !!e)) {
    return fieldErrors;
  }

  const { attribute, period, action } = values;
  const property = buildPropertyString(attribute, period, action);

  if (!availableProperties.find((x) => x.name === property)) {
    return { property: 'This combination is currently unsupported' };
  }
};

type FormValues = {
  attribute: string;
  period: string;
  action: string;
  property: string;
};

interface Props {
  handleClose: () => void;
  onSubmit: (property: string) => void;
  availableProperties: AvailableProperty[];
}

const SimpleVelocityForm: React.FunctionComponent<Props> = ({
  handleClose,
  onSubmit,
  availableProperties,
}) => {
  return (
    <Formik<FormValues>
      initialValues={{
        attribute: '',
        period: velocityPeriodOptions[0].value,
        action: transactionActionOptions[0].value,
        property: '',
      }}
      onSubmit={({ attribute, period, action }) => {
        const property = `:${buildPropertyString(attribute, period, action)}:`;

        onSubmit(property);
      }}
      validate={simpleVelocityValidator(availableProperties)}
      validateOnMount
    >
      {({ isValid, errors }) => {
        return (
          <Form>
            <VelocityFormHeader
              velocityFormTitle="Transaction count velocity"
              handleClose={handleClose}
            />
            <Flex flexDirection="column" p={4}>
              <Text mb={2}>Insert a velocity element to check:</Text>
              <SpaceBetween my={3}>
                <Box>
                  <FormLabel fontSize={1}>The number of times a ...</FormLabel>
                  <FormikSelectMenu
                    name="attribute"
                    placeholder="Select an attribute"
                    options={simpleVelocityProperties}
                  />
                </Box>
                <Box>
                  <FormikSegmentedControl
                    label="Appears over a period of ..."
                    name="period"
                    options={velocityPeriodOptions}
                  />
                </Box>
                <Box>
                  <FormikSegmentedControl
                    label="in all transactions that were ..."
                    name="action"
                    options={transactionActionOptions}
                  />
                </Box>
              </SpaceBetween>
              <Box mt={5}>
                <TooltipButtonWrapper enabled={!isValid} text={errors.property}>
                  <PrimaryButton type="submit" disabled={!isValid}>
                    Insert velocity element
                  </PrimaryButton>
                </TooltipButtonWrapper>
              </Box>
            </Flex>
          </Form>
        );
      }}
    </Formik>
  );
};

export default SimpleVelocityForm;
