import React from 'react';
import { Text, Flex, ClickableElement } from '@cko/primitives';
import styled from 'styled-components';
import { IconActionCross } from '@cko/icons';

interface Props {
  handleClose: () => void;
  velocityFormTitle: string;
}

export const TitleWrapper = styled(Flex)`
  align-items: center;
  justify-content: space-between;
  text-align: left;
  background: white;
`;

const VelocityFormHeader: React.FunctionComponent<Props> = ({
  velocityFormTitle,
  handleClose,
}) => {
  return (
    <TitleWrapper p={3} borderBottom="baseLight.1">
      <Flex>
        <Text fontSize="14px" lineHeight="16px">
          {velocityFormTitle}
        </Text>
      </Flex>
      <Flex>
        <ClickableElement
          onClick={(e) => {
            e.preventDefault();
            handleClose();
          }}
        >
          <IconActionCross />
        </ClickableElement>
      </Flex>
    </TitleWrapper>
  );
};

export default VelocityFormHeader;
