import React from 'react';
import { Panel, Box, Flex, Text, Link } from '@cko/primitives';
import Grid from 'components/Grid';
import DataTable from 'components/DataTable';
import RulesHeaderContainer from '../RulesHeaderContainer';
import { ScoringRule, FormMode } from 'types/models';
import DeleteRuleContainer from 'containers/DeleteRuleContainer';
import { useRulesData } from 'hooks/api/rules';
import { ErrorPanel } from 'components/ErrorScreen/ErrorScreen';
import RulesBuilderContainer from 'features/RulesBuilder/RulesBuilderContainer';
import { useRulesBuilderModal } from 'features/RulesBuilder/RulesBuilderContainer/RulesBuilderContainer';

export const deserializeTags = (tags: string[]) => tags.join(', ');

const RulesContainer = () => {
  const { data, error } = useRulesData();

  const { openModal } = useRulesBuilderModal();

  if (!data) {
    return null;
  }

  if (error) {
    return (
      <Flex
        justifyContent="center"
        alignItems="center"
        height="calc(100vh - 120px)"
      >
        <ErrorPanel>
          <Text fontSize={1} lineHeight={1}>
            Something went wrong. Please try again or you can get in touch with
            the support team at:
            <Link href="mailto:prism-support@checkout.com">
              prism-support@checkout.com
            </Link>
          </Text>
        </ErrorPanel>
      </Flex>
    );
  }

  const transformedData = data.summaries.map((x) => ({
    ...x,
    tags: deserializeTags(x.tags),
  }));

  return (
    <>
      <Panel>
        <Grid gridTemplateRows="50px 75vh 60px">
          <Box py={2} px={3}>
            <RulesHeaderContainer />
          </Box>
          <DataTable<ScoringRule>
            onInteraction={(item) =>
              openModal({
                ...item,
                mode: FormMode.EditDelete,
                modalId: 'Rule:RulesContainer',
              })
            }
            tableHeaders={['Rule name', 'Tags']}
            items={transformedData}
            itemsToDisplay={['name', 'tags']}
            cyTag="blacklists-master-details-values"
          />
        </Grid>
      </Panel>
      <RulesBuilderContainer modalId="Rule:RulesContainer" />
      <DeleteRuleContainer />
    </>
  );
};

export default RulesContainer;
