import { useContext, useMemo, useState, useEffect } from 'react';
import React from 'react';

const ScrollSpyContext = React.createContext<
  | {
      refs: Record<string, { ref: React.RefObject<HTMLDivElement> }>;
      scrollTo: (key: string, opts: { root: Window | Element }) => void;
      isInView: (key: string) => boolean;
    }
  | undefined
>(undefined);

export const ScrollSpyProvider: React.FC<{ keys: string[] }> = ({
  keys,
  children,
}) => {
  const [isInView, setIsInView] = useState<Record<string, boolean>>({});

  const refs = useMemo(
    () =>
      keys.reduce((acc, curr) => {
        const ref = React.createRef<HTMLDivElement>();

        acc[curr] = {
          ref,
          observer: new IntersectionObserver(([entry]) => {
            setIsInView((prev) => ({
              ...prev,
              [curr]: entry.isIntersecting,
            }));
          }),
        };
        return acc;
      }, {} as Record<string, { ref: React.RefObject<HTMLDivElement>; observer: IntersectionObserver }>),
    [keys]
  );

  useEffect(() => {
    Object.values(refs).forEach(({ ref, observer }) => {
      if (ref.current) {
        observer.observe(ref.current);
      }
    });

    return () => {
      Object.values(refs).forEach(({ observer }) => {
        observer.disconnect();
      });
    };
  }, [refs]);

  const scrollTo = (
    key: keyof typeof refs,
    opts: { root: any } = { root: window }
  ) => {
    const obj = refs[key];
    if (obj?.ref.current) {
      opts.root.scrollTo({
        top: obj.ref.current.offsetTop + 1,
        behavior: 'smooth',
      });
    }
  };

  return (
    <ScrollSpyContext.Provider
      value={{
        refs,
        scrollTo,
        isInView: (key: string) => isInView[key],
      }}
    >
      {children}
    </ScrollSpyContext.Provider>
  );
};

export const useScrollSpy = (
  {
    getRoot,
  }: { getRoot: (el: Element) => Window | Element | null | undefined } = {
    getRoot: () => window,
  }
) => {
  const context = useContext(ScrollSpyContext);

  if (context === undefined) {
    throw new Error('useScrollTo must be used within a ScrollSpyProvider');
  }

  return {
    ...context,
    scrollTo: (type: string) => {
      const obj = context.refs[type];
      if (obj?.ref.current) {
        context.scrollTo(type, { root: getRoot(obj?.ref.current) ?? window });
      }
    },
  };
};
