import React, { useMemo } from 'react';
import { ListItem } from '@cko/primitives';
import styled from 'styled-components';
import RulesExample from '../../RulesEditor/RulesExample';
import useMlScoreEnabled from 'hooks/useMlScoreEnabled';
import usePropertyList from '../usePropertyList';
import { RuleProperties } from 'types/models';
import RulesMenuItemWrapper from '../../rulesBuilderUtils/RulesMenuItemWrapper';

interface RuleSidebarItemProps {
  predicate: string;
}

const RuleSidebarItem = React.memo(({ predicate }: RuleSidebarItemProps) => {
  return (
    <RulesMenuItemWrapper>
      <RulesExample predicate={predicate} />
    </RulesMenuItemWrapper>
  );
});

const scorePredicateRegularExpression = RegExp(':score:', 'g');

const filterScorePredicate = (exampleCombination: RuleProperties) => {
  return (
    exampleCombination.predicate &&
    !scorePredicateRegularExpression.test(exampleCombination.predicate)
  );
};

interface Props {
  data: Record<string, RuleProperties> | {};
}

const RulesPropertyListItem = styled(ListItem)`
  cursor: pointer;
  word-break: break-word;
`;

const FilteredRulesPropertyListItems: React.FunctionComponent<Props> = ({
  data,
}) => {
  const getPropertyListProps = usePropertyList();

  const enabledSelectedMerchantMlScore = useMlScoreEnabled();

  const filteredRulesProperties = useMemo(() => {
    return enabledSelectedMerchantMlScore
      ? Object.values(data)
      : Object.values(data).filter(filterScorePredicate);
  }, [data, enabledSelectedMerchantMlScore]);

  return (
    <ul>
      {filteredRulesProperties.map((ruleProperty) => {
        return (
          ruleProperty.predicate && (
            <RulesPropertyListItem
              key={ruleProperty.predicate}
              {...getPropertyListProps(ruleProperty)}
              data-cy={`property-list:${ruleProperty.predicate}`}
            >
              <RuleSidebarItem predicate={ruleProperty.predicate} />
            </RulesPropertyListItem>
          )
        );
      })}
    </ul>
  );
};

export default FilteredRulesPropertyListItems;
