import React, { useState } from 'react';
import { Flex, ClickableElement, Text } from '@cko/primitives';
import { Tooltip } from '@cko/tooltip';
import RulesMenuItemWrapper from '../rulesBuilderUtils/RulesMenuItemWrapper';
import { Strong } from 'components/Text';
import styled from 'styled-components';
import { lighten } from 'polished';

interface Props {
  title: string;
  description: string;
  content: string;
  onClick: () => void;
  isActive: boolean;
  onMouseEnter: () => void;
  onMouseLeave: () => void;
}

const PredicateButton = styled(ClickableElement)<{ isActive: boolean }>(
  ({ theme, isActive }) => `
  width: 100%;
  text-align: start;
  padding: 4px 8px;
  border-radius: 6px;
  color: ${isActive ? theme.colors.white : theme.colors.warningDark};
  background-color:  ${
    isActive ? theme.colors.base : theme.colors.warningLight
  };
  font-size:${theme.fontSizes[1]};
  &:hover {
    color: ${theme.colors.warningDark};
    background-color:
      ${lighten(0.03, theme.colors.warningLight)};
  }
`
);

const VelocityPropertyItem: React.FunctionComponent<Props> = ({
  title,
  isActive,
  description,
  content,
  onClick,
  onMouseEnter,
  onMouseLeave,
}) => {
  const [showTooltip, setShowTooltip] = useState(false);

  return (
    <RulesMenuItemWrapper>
      <Flex flexDirection="column">
        <Text textAlign="left" fontSize={1} lineHeight={1}>
          <Strong>{title} </Strong> {description}
        </Text>
        <Tooltip show={!isActive && showTooltip} text="Click to launch wizard">
          <PredicateButton
            isActive={isActive}
            onClick={onClick}
            onMouseEnter={() => {
              onMouseEnter();
              setShowTooltip(true);
            }}
            onMouseLeave={() => {
              onMouseLeave();
              setShowTooltip(false);
            }}
          >
            {content}
          </PredicateButton>
        </Tooltip>
      </Flex>
    </RulesMenuItemWrapper>
  );
};

export default VelocityPropertyItem;
