import { Box, PrimaryButton, SpaceBetween, Text, Flex } from '@cko/primitives';
import { Formik, Form } from 'formik';
import React from 'react';
import FormikSelectMenu from 'components/FormikSelectMenu';
import { createValidator, required, duplicate } from 'utils/validator';
import VelocityFormHeader from '../VelocityFormHeader';

import {
  relativeVelocityOptions,
  velocityPeriodOptions,
  relativeVelocityTransactionActionOptions,
} from './../velocityProperties';
import FormikSegmentedControl from 'components/FormikSegmentedControl';
import { AvailableProperty } from 'types/models';
import TooltipButtonWrapper from 'components/TooltipButtonWrapper';

export interface RelativeVelocityFormData {
  attributeOne: string;
  attributeTwo: string;
  period: string;
  action: string;
  property: string;
}

const validator = createValidator({
  attributeOne: [
    required('Attribute one is required'),
    duplicate('attributeTwo', 'The attributes must be different'),
  ],
  attributeTwo: [required('Attribute two is required')],
  period: [required()],
  action: [required()],
});

const relativeVelocityValidator = (
  availableProperties: AvailableProperty[]
) => async (values: RelativeVelocityFormData) => {
  const fieldErrors = await validator(values);

  if (Object.values(fieldErrors).find((e) => !!e)) {
    return fieldErrors;
  }

  const { attributeOne, attributeTwo, period, action } = values;
  const property = createRelativeVelocityPredicate({
    action,
    period,
    attributeOne,
    attributeTwo,
  });

  if (!availableProperties.find((x) => x.name === property)) {
    return { property: 'This combination is currently unsupported' };
  }
};

interface Props {
  onSubmit: (velocityProperty: string) => void;
  handleClose: () => void;
  availableProperties: AvailableProperty[];
}

interface Params {
  action: string;
  attributeOne: string;
  attributeTwo: string;
  period: string;
}

export const createRelativeVelocityPredicate = ({
  action,
  attributeOne,
  attributeTwo,
  period,
}: Params) => {
  return `nb_relative_${action}_${attributeOne}_per_${attributeTwo}_${period}`;
};

const RelativeVelocityForm: React.FunctionComponent<Props> = ({
  onSubmit,
  handleClose,
  availableProperties,
}) => {
  return (
    <Formik<RelativeVelocityFormData>
      initialValues={{
        attributeOne: '',
        attributeTwo: '',
        period: velocityPeriodOptions[0].value,
        action: relativeVelocityTransactionActionOptions[0].value,
        property: '',
      }}
      onSubmit={({ action, period, attributeOne, attributeTwo }) => {
        onSubmit(
          `:${createRelativeVelocityPredicate({
            action,
            period,
            attributeOne,
            attributeTwo,
          })}:`
        );
      }}
      validate={relativeVelocityValidator(availableProperties)}
      validateOnMount
    >
      {({ errors, isValid }) => {
        const [firstError] = Object.values(errors);

        return (
          <Form>
            <VelocityFormHeader
              velocityFormTitle="Relative velocity"
              handleClose={handleClose}
            />
            <Flex flexDirection="column" p={4}>
              <SpaceBetween my={3}>
                <Text mb={2} color="focus">
                  Insert a velocity element to check:
                </Text>
                <Box>
                  <FormikSelectMenu
                    name="attributeOne"
                    options={relativeVelocityOptions}
                    label="The number of unique ..."
                    placeholder="Select attribute 1"
                  />
                </Box>

                <Box>
                  <FormikSelectMenu
                    name="attributeTwo"
                    options={relativeVelocityOptions}
                    label="sharing ..."
                    placeholder="Select attribute 2"
                  />
                </Box>

                <Box>
                  <FormikSegmentedControl
                    label="Appears over a period of ..."
                    name="period"
                    options={velocityPeriodOptions}
                  />
                </Box>
                <Box>
                  <FormikSegmentedControl
                    label="in all transactions that were ..."
                    name="action"
                    options={relativeVelocityTransactionActionOptions}
                  />
                </Box>
              </SpaceBetween>

              <Box mt={5}>
                <TooltipButtonWrapper enabled={!isValid} text={firstError}>
                  <PrimaryButton type="submit" disabled={!isValid}>
                    Insert velocity element
                  </PrimaryButton>
                </TooltipButtonWrapper>
              </Box>
            </Flex>
          </Form>
        );
      }}
    </Formik>
  );
};

export default RelativeVelocityForm;
