import React, { useCallback } from 'react';
import { RenderLeafProps, RenderElementProps } from 'slate-react';
import Highlighted, { CustomLeaf, RuntimeType } from 'components/Highlighted';
import { AvailableProperty } from 'types/models';

const stripFirstAndLast = (text: string) => text.replace(/:/g, '');

const useSyntaxHighlighting = () => {
  const getRuntimeType = useCallback(
    (text: string, availableProperties: AvailableProperty[]) => {
      return availableProperties.find((x) => x.name === text)
        ?.type as RuntimeType;
    },
    []
  );

  const getLeafRenderer = useCallback(
    (availableProperties: AvailableProperty[]) => (props: RenderLeafProps) => (
      <Highlighted
        getRuntimeType={(leaf: CustomLeaf) => {
          if (leaf.type === 'Property') {
            const runtimeType = getRuntimeType(
              stripFirstAndLast(leaf.value),
              availableProperties
            );

            return runtimeType;
          }
          return null;
        }}
        {...props}
      />
    ),
    [getRuntimeType]
  );

  const renderElement = useCallback((props: RenderElementProps) => {
    const { attributes, children } = props;
    return (
      <p contentEditable={false} style={{ lineHeight: '30px' }} {...attributes}>
        {children}
      </p>
    );
  }, []);

  return { getLeafRenderer, renderElement };
};

export default useSyntaxHighlighting;
