import React, { useState, lazy, Suspense, useMemo } from 'react';
import { Form, useFormikContext } from 'formik';
import {
  Flex,
  PrimaryButton,
  Box,
  DangerButton,
  InlineNotification,
} from '@cko/primitives';
import TextField from 'components/TextField';
import {
  FormMode,
  ScoringRuleFormData,
  ScoringRuleModalData,
} from 'types/models';

import EditorFallback from '../RulesEditor/EditorFallback';
import styled from 'styled-components';
import { IconActionDelete, IconActionCheckmark } from '@cko/icons';
import useModalManager from 'hooks/useModalManager';
import { Modals } from 'types/enums/modals';
import TooltipButtonWrapper from 'components/TooltipButtonWrapper';
import SpacedButtonGroup from 'components/SpacedButtonGroup';
import FeatureFlag from 'components/FeatureFlag';
import useIntentHover from 'hooks/useIntentHover';
import { useRulesBuilderHelp } from '../RulesBuilderHelpContext';
import RulebuilderContent from '../RuleBuilderContent';
import 'styled-components/macro';
import { useRulesBuilder } from '../RulesBuilderContext';

const IconCheckmarkIcon = styled(IconActionCheckmark)`
  & > g {
    fill: #017c63;
  }
`;

const RulesEditorContainer = lazy(
  () => import(/* webpackChunkName: "RulesEditor" */ '../RulesEditorContainer')
);

const DeleteIcon = styled(IconActionDelete)`
  margin-right: 8px;
  vertical-align: text-bottom;
`;

const EditorLabel = styled.span`
  display: inline-block;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  margin: 4px 0;
  margin-top: 0;
`;

interface Props {
  data: ScoringRuleModalData;
}

const RulesBuilderForm: React.FunctionComponent<Props> = ({ data }) => {
  const initialCount = data.mode === FormMode.Edit ? 1 : 0;
  const [testCount, setTestCount] = useState(initialCount);
  const getIntentProps = useIntentHover();
  const { setHelpContentKey } = useRulesBuilderHelp();
  const { submissionError, setSubmissionError } = useRulesBuilder();

  const { openModal: openDeleteModal } = useModalManager<ScoringRuleModalData>(
    Modals.DELETE_SCORING_RULE
  );

  const initial = {
    name: data.name || '',
    predicate: data.predicate || '',
  };

  const isDisabled = useMemo(
    () => !!(data.risk_profiles && data.risk_profiles.length > 0),
    [data.risk_profiles]
  );

  const buttonText =
    data.mode === FormMode.Edit || data.mode === FormMode.EditDelete
      ? 'Update rule'
      : 'Create rule';

  const {
    isSubmitting,
    submitCount,
    errors,
    validateForm,
    isValid,
    isValidating,
  } = useFormikContext<ScoringRuleFormData>();

  const submitAndTestCount = submitCount + testCount;
  const [firstError] = Object.values(errors);

  return (
    <Box width={1}>
      <Form data-testid="add-rule-form">
        <Box mb={4}>
          <TextField
            label="Rule name"
            name="name"
            submitCount={submitAndTestCount}
            displayFieldError={false}
          />
        </Box>
        <Box mb={3}>
          <EditorLabel>Rule</EditorLabel>
          <Suspense fallback={<EditorFallback />}>
            <RulesEditorContainer
              name="predicate"
              initialValue={initial.predicate}
              submitAndTestCount={submitAndTestCount}
            />
          </Suspense>
        </Box>

        <FeatureFlag
          on={
            firstError !== undefined &&
            submitAndTestCount > initialCount &&
            !isValidating &&
            submissionError === null
          }
        >
          <Box width={[2 / 3]} mb={3}>
            <InlineNotification type="alert">{firstError}</InlineNotification>
          </Box>
        </FeatureFlag>

        <FeatureFlag
          on={
            submitAndTestCount > initialCount &&
            firstError === undefined &&
            !isValidating &&
            submissionError === null
          }
        >
          <Box width={[2 / 3]} mb={3}>
            <Flex p={0}>
              <InlineNotification type="success">
                <Flex>
                  <Box mr={2}>
                    <IconCheckmarkIcon />
                  </Box>
                  Rule syntax is consistent
                </Flex>
              </InlineNotification>
            </Flex>
          </Box>
        </FeatureFlag>

        <FeatureFlag on={submissionError !== null}>
          <Box width={[2 / 3]} mb={3}>
            <InlineNotification type="alert">
              {submissionError}
            </InlineNotification>
          </Box>
        </FeatureFlag>

        <Flex justifyContent="space-between">
          <PrimaryButton
            variant="large"
            onClick={async (e) => {
              e.preventDefault();
              setTestCount((c) => c + 1);
              setSubmissionError(null);
              validateForm();
            }}
            {...getIntentProps(() => {
              setHelpContentKey(RulebuilderContent.editor.checkRule.helpKey);
            })}
          >
            {isValidating ? 'Checking rule' : 'Check rule'}
          </PrimaryButton>

          <SpacedButtonGroup>
            <FeatureFlag on={errors.predicate === undefined && isValid}>
              <PrimaryButton variant="large" type="submit">
                {isSubmitting ? 'Submitting' : buttonText}
              </PrimaryButton>
            </FeatureFlag>

            {data.mode === FormMode.EditDelete && (
              <TooltipButtonWrapper
                enabled={isDisabled}
                text="This rule cannot be deleted as it is currently being used in one or more Risk Profiles."
              >
                <DangerButton
                  variant="large"
                  disabled={isDisabled}
                  onClick={(e) => {
                    e.preventDefault();
                    openDeleteModal({ ...data });
                  }}
                >
                  <DeleteIcon />
                  Delete Rule
                </DangerButton>
              </TooltipButtonWrapper>
            )}
          </SpacedButtonGroup>
        </Flex>
      </Form>
    </Box>
  );
};

export default RulesBuilderForm;
