import endpoints from 'api/endpoints';
import { queryCache } from 'react-query';
import { useDeleteMutation } from '../common/useDeleteMutation';

export const useDeleteList = (onSuccess: () => void) => {
  return useDeleteMutation(endpoints.lists, {
    onSuccess: () => {
      queryCache.removeQueries(endpoints.lists);
      onSuccess();
    },
  });
};
