import React from 'react';
import useTabs from 'hooks/useTabs';
import BaseTab from './BaseTab';

interface Props {
  index?: number;
  disabled?: boolean;
  id: string;
}

const Tab: React.FunctionComponent<Props> = ({
  children,
  index,
  disabled,
  id,
}) => {
  const { getTabProps } = useTabs();

  if (index === undefined) {
    return null;
  }

  return (
    <li>
      <BaseTab {...getTabProps({ id, index, disabled })}>{children}</BaseTab>
    </li>
  );
};

export default Tab;
