import React, { useState } from 'react';
import useModalManager from 'hooks/useModalManager';
import { Modals } from 'types/enums/modals';
import useAlerts from 'hooks/useAlerts';
import DeleteConfirmationModal from 'components/DeleteConfirmationModal/DeleteConfirmationModal';
import { useDeleteRule } from 'hooks/api/rules';
import { queryCache } from 'react-query';
import endpoints from 'api/endpoints';
import { useRulesBuilderModal } from 'features/RulesBuilder/RulesBuilderContainer/RulesBuilderContainer';

interface ModalData {
  name: string;
  id: string;
}

const DeleteRuleContainer = () => {
  const [submissionErrorMessage, setSubmissionError] = useState<string | null>(
    null
  );
  const [loading, setLoading] = useState(false);

  const { modalData, closeModal } = useModalManager<ModalData>(
    Modals.DELETE_SCORING_RULE
  );

  const { closeModal: closeRuleModal } = useRulesBuilderModal();

  const { showAlert } = useAlerts();

  const handleClose = () => {
    setSubmissionError(null);
    closeModal();
    closeRuleModal();
  };

  const handleSuccess = () => {
    showAlert('Rule successfully deleted');
    handleClose();
    queryCache.invalidateQueries(endpoints.rules);
  };

  const [deleteRule] = useDeleteRule(handleSuccess);

  if (!modalData) {
    return null;
  }

  return (
    <DeleteConfirmationModal
      itemType="rule"
      handleClose={handleClose}
      submissionErrorMessage={submissionErrorMessage}
      loading={loading}
      itemName={modalData.name}
      onCancel={handleClose}
      onDelete={async () => {
        setLoading(true);
        try {
          await deleteRule(`${modalData.id}`);
          setLoading(false);
        } catch (error) {
          if (error.response?.status === 409) {
            setSubmissionError(
              `This rule is referenced in Routing and cannot be deleted.`
            );
          } else {
            setSubmissionError('Technical error. Please try again later.');
          }
          setLoading(false);
        }
      }}
    />
  );
};

export default DeleteRuleContainer;
