import React, { useMemo } from 'react';
import { withReact, Slate } from 'slate-react';
import { createEditor } from 'slate';
import useSyntaxHighlighting from 'hooks/useSyntaxHighlighting';
import SyntaxHighlightEditable from './SyntaxHighlightEditable';
import { tokenize } from './antlrUtils';
import { useRulesBuilder } from '../RulesBuilderContext';
import { AvailableProperty } from 'types/models';

interface Props {
  predicate: string;
}

export const RuleExample: React.FunctionComponent<
  Props & { availableProperties: AvailableProperty[] }
> = ({ predicate, availableProperties }) => {
  const { getLeafRenderer, renderElement } = useSyntaxHighlighting();
  const editor = useMemo(() => withReact(createEditor()), []);

  const renderLeaf = useMemo(() => getLeafRenderer(availableProperties), [
    availableProperties,
    getLeafRenderer,
  ]);

  return (
    <Slate
      onChange={() => {}}
      editor={editor}
      value={[
        {
          children: [{ text: predicate }],
        },
      ]}
    >
      <SyntaxHighlightEditable
        tabIndex={-1}
        contentEditable={false}
        spellCheck="false"
        tokensToSkip={['OTHERS', 'WHITESPACE']}
        tokenize={tokenize}
        renderElement={renderElement}
        renderLeaf={renderLeaf}
      />
    </Slate>
  );
};

const RulesExampleContainer: React.FunctionComponent<Props> = ({
  predicate,
}) => {
  const { availableProperties } = useRulesBuilder();

  return (
    <RuleExample
      predicate={predicate}
      availableProperties={availableProperties}
    />
  );
};

export default React.memo(RulesExampleContainer);
