import React, {
  useContext,
  createContext,
  useMemo,
  useState,
  useCallback,
  useRef,
  useEffect,
} from 'react';

export enum VelocityWizardFormType {
  VelocityTransactionCount = 'velocity_transaction_count',
  VelocityRelative = 'velocity_relative',
}

export interface VelocityWizardFormContext {
  formType: VelocityWizardFormType | null;
  onFormTypeSelected: (formType: VelocityWizardFormType) => void;
  isDrawerOpen: boolean;
  handleClose: () => void;
  drawerRef: React.Ref<HTMLDivElement> | null;
}

interface Props {
  children: React.ReactNode;
}

export const VelocityWizardContext = createContext<
  VelocityWizardFormContext | undefined
>(undefined);

export const VelocityWizardProvider: React.FunctionComponent<Props> = ({
  children,
}) => {
  const [formType, setFormType] = useState<VelocityWizardFormType | null>(null);
  const [isDrawerOpen, setDrawerOpen] = useState(false);
  const drawerRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const tempRef = drawerRef.current;

    const handleEnd = () => {
      if (!isDrawerOpen) {
        setFormType(null);
      }
    };
    tempRef?.addEventListener('transitionend', handleEnd);
    return () => {
      tempRef?.removeEventListener('transitionend', handleEnd);
    };
  }, [isDrawerOpen]);

  const onFormTypeSelected = useCallback(
    (formTypeEnum: VelocityWizardFormType) => {
      setFormType(formTypeEnum);
      setDrawerOpen((isOpen) => {
        if (!isOpen) {
          return true;
        } else if (isOpen && formTypeEnum === formType) {
          return false;
        }
        return isOpen;
      });
    },
    [formType]
  );

  const handleClose = useCallback(() => {
    setDrawerOpen(false);
  }, []);

  const value = useMemo(() => {
    return {
      formType,
      handleClose,
      onFormTypeSelected,
      isDrawerOpen,
      drawerRef,
    };
  }, [formType, isDrawerOpen, onFormTypeSelected, handleClose]);

  return (
    <VelocityWizardContext.Provider value={value}>
      {children}
    </VelocityWizardContext.Provider>
  );
};

export const useVelocityWizard = () => {
  const context = useContext(VelocityWizardContext);

  if (context === undefined) {
    throw new Error(
      'useVelocityWizard must be used within a VelocityWizardProvider'
    );
  }

  return context;
};
